.title {
  margin: 0;
  margin-bottom: 10px;
  color: var(--main-text-color);
}
.breadcrumbs {
  display: block;
  padding-bottom: 10px;
}

.container {
  position: relative;
}

.loader {
  width: 100%;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.loaderActive {
  color: var(--main-text-color);
  background: var(--grey-color);
  font-size: 14px;
  border-radius: 10px;
  opacity: 0.3;

  padding: 10px;
  margin: 5px 0;
}

.section {
  color: var(--main-text-color);
  background: var(--grey-color);
  font-size: 14px;
  border-radius: 10px;

  padding: 10px;
  margin: 5px 0;
}

.infoDateStatus {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .container {
    display: grid;
    column-gap: 10px;

    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1368px) {
  .container {
    display: grid;
    column-gap: 10px;

    grid-template-columns: repeat(3, 1fr);
  }
}
